<template>
    <layout :showBack="isEdit" :title="`${isEdit ? '编辑' : '新增'}供应商信息`">

        <div class="edit-container">
            <span class="title">基本信息</span>
            <van-form @submit="onSubmit">
                <van-field v-model="object.name"
                    name="材料名字"
                    label="材料名字"
                    placeholder="请输入材料名字"
                    :rules="rules.name"/>
                <!-- clearable在添加了readonly的时候会失效 -->
                <van-field v-model="address"
                    name="地市"
                    label="地市"
                    clearable
                    readonly
                    placeholder="请选择地市"
                    :rules="rules.address">
                    <template #button>
                        <van-button size="small" @click="show = true" native-type="button" type="primary">选择地市</van-button>
                    </template>
                </van-field>
                <van-field v-model="object.address"
                    name="详细地址"
                    label="详细地址"
                    placeholder="请输入详细地址"/>
                <van-field v-model="object.code"
                    name="信用码"
                    label="信用码"
                    placeholder="请输入信用码"
                    :rules="rules.code"/>
                <van-field v-model="object.contact"
                    name="联系人"
                    label="联系人"
                    placeholder="请输入联系人"/>
                <van-field v-model="object.phone"
                    name="联系方式"
                    label="联系方式"
                    placeholder="请输入联系方式"
                    :rules="rules.phone"/>
                <van-field v-model="object.remark"
                    name="备注"
                    label="备注"
                    placeholder="请输入备注"/>
                <div class="btn">
                    <van-button round block type="primary" native-type="submit">提交</van-button>
                </div>
            </van-form>
        </div>
        
        <van-popup v-model="show" position="bottom" :style="{ height: '308px' }">
            <van-area title="标题"
                @cancel="show = false"
                @confirm="confirmEvt"
                :area-list="areaList" />
        </van-popup>
    </layout>
</template>

<script>
    import { areaList } from '@vant/area-data'
    import { supplierAddApi, supplierSaveApi } from '../../apis/SupplierApi'

    export default {
        data() {
            return {
                isEdit: false,
                address: '',
                object: {
                    id: '',
                    name: '',
                    province: '',
                    city: '',
                    county: '',
                    address: '',
                    code: '',
                    contact: '',
                    phone: '',
                    remark: ''
                },
                rules: {
                    name: [{required: true, message: '供应商名字必须输入'}],
                    address: [{required: true, message: '供应商名字必须输入'}],
                    code: [{required: true, message: '社会信用码必须输入'}],
                    phone: [
                        {validator: val => {
                            return /^1[3-9]{1}[0-9]{9}$/g.test(val)
                        }, message: '联系方式不正确'}
                    ]
                },
                areaList,
                isEdit: false,
                show: false
            }
        },
        created() {
            let obj = this.$route.query,
                cache = this.$store.getters['common/getEditData']

            this.isEdit = !!obj.id
            if (this.isEdit) {
                for (let name in this.object) {
                    this.object[name] = cache[name] || ''
                }
                this.address = cache.province + '/' + cache.city + '/' + cache.county
            }
        },
        methods: {
            async onSubmit() {
                let _arr = this.address.split('/')
                this.object.province = _arr[0]
                this.object.city = _arr[1]
                this.object.county = _arr[2]

                let result
                if (this.isEdit) {
                    result = await supplierSaveApi(this.object)
                } else {
                    result = await supplierAddApi(this.object)
                }
                if (result.code === 200) {
                    this.$toast.success(`${this.isEdit ? '编辑' : '新增'}供应商信息成功`)
                } else {
                    this.$toast.fail(`${this.isEdit ? '编辑' : '新增'}供应商信息失败`)
                }
            },
            confirmEvt(data) {
                this.address = data.reduce((nl, v) => {
                    nl.push(v.name)
                    return nl
                }, []).join('/')
                this.show = false
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../style/common.less";

    .edit-container {
        > .title {
            display: block;
            font-size: .7rem;
            line-height: 42px;
            color: @font-8c;
        }
        display: block;
        padding: 12px 12px 0;
        .btn {
            margin-top: 16px;
        }
    }
</style>